import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {HomeCompomentComponent} from './home-compoment/home-compoment.component';
import {AboutCompomentComponent} from './about-compoment/about-compoment.component';
import {TranAssignStockComponent} from './tran-assign-stock/tran-assign-stock.component';
import {SuperStockistComponent} from './super-stockist/super-stockist.component';
import {DistributorComponent} from './distributor/distributor.component';
import {RetailerComponent} from './retailer/retailer.component';
import {BusinessAssociateComponent} from './business-associate/business-associate.component';
import {SuperStockistListComponent} from './super-stockist-list/super-stockist-list.component';

const routes: Routes = [
  {path :'home', component :HomeCompomentComponent},
  {path :'about', component :AboutCompomentComponent},
  {path :'Transaction', component :TranAssignStockComponent},
  {path :'SuperStockist', component :SuperStockistListComponent},
  {path :'Drisbutor', component :DistributorComponent},
  {path :'Retailer', component :RetailerComponent},
  {path :'BusinessAssociate', component :BusinessAssociateComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
