import { Component, OnInit } from '@angular/core';
import { SuperStockistService } from '../service/super-stockist.service';
import { SuperStockist } from '../model/SuperStockist';

@Component({
  selector: 'app-super-stockist',
  templateUrl: './super-stockist.component.html',
  styleUrls: ['./super-stockist.component.css']
})
export class SuperStockistComponent implements OnInit {

  superStockists: SuperStockist[];
  searchText: string = '';

  constructor(private superStockistService: SuperStockistService) { }

  ngOnInit() {

  }

  loadData() {
    this.searchText = '';
    this.superStockistService.getDataAll('')
      .subscribe(data => {
        this.superStockists = data as SuperStockist[];
        (err) => { console.log(err) };
      });
  }

}
