import { Pipe, PipeTransform } from '@angular/core';


@Pipe({
  name: 'filterByMatchText'
})
export class FilterByMatchTextPipe implements PipeTransform {

  transform(value: any[], criteria: string): any {
    if(!value) return [];
    if(!criteria) return value;   
    
    return value.filter(i=>{
      for (let key in i ) {       
        
        if((""+i[key]).includes(criteria))
           return true;
    }
  });

  }

}
