import { Component, OnInit } from '@angular/core';
import { SuperStockistService } from '../service/super-stockist.service';
import { SuperStockist } from '../model/SuperStockist';

import { AppSettingsService } from '../service/app-settings.service';
import { MyAppSetting } from '../config/MyAppSetting';


@Component({
  selector: 'app-super-stockist-list',
  templateUrl: './super-stockist-list.component.html',
  styleUrls: ['./super-stockist-list.component.css']
})
export class SuperStockistListComponent implements OnInit {

  myAppSetting: MyAppSetting;
  superStockists: SuperStockist[];
  searchText: string = '';

  constructor(private superStockistService: SuperStockistService,private appSettingsService: AppSettingsService) { 
    appSettingsService.getSettings().subscribe(data => this.myAppSetting = data);
    
  }

  ngOnInit() {
    this.loadData();
  }

  loadData() {
    this.searchText = '';
    this.superStockistService.getDataAll('05008166-6159-493D-15EE-08D712D81DBC')
      .subscribe(data => {
        console.log(data)
        this.superStockists = data as SuperStockist[];
        (err) => { console.log(err) };
      });
  }
}
